<template>
  <div class="user_info mypage_edit">
    <div class="wrap">
      <h2>
        <img src="/images/svg/user_info_h1.svg" alt="회원정보수정 타이틀">
        <p></p>
      </h2>

      <section class="mt30">
        <ValidationObserver ref="observer" tag="div" v-slot="{ invalid }">
          <p><span class="circle">●</span> 이름</p>
          <InputWithValidation rules="required|name"
                               type="text"
                               ref="name"
                               title="이름"
                               name="form.name"
                               v-model="form.name"
                               autocomplete="off"
                               placeholder="이름을 입력하세요."/>
          <p><span class="circle">●</span> 이메일</p>
          <InputWithValidation rules="required|email"
                               type="text"
                               ref="email"
                               title="이메일"
                               name="form.email"
                               v-model="form.email"
                               autocomplete="off"
                               placeholder="이메일을 입력해주세요."/>
          <p><span class="circle">●</span> 아이디</p>
          <InputWithValidation rules="required|id"
                               type="text"
                               ref="id"
                               title="아이디"
                               name="form.userId"
                               v-model="form.userId"
                               autocomplete="off"
                               placeholder="아이디 (대소문자/숫자 (2~14자리))"/>
          <p><span class="circle">●</span> 비밀번호</p>
          <InputWithValidation rules="required|password"
                               type="text"
                               ref="pw"
                               title="비밀번호"
                               name="form.password"
                               v-model="form.password"
                               autocomplete="off"
                               placeholder="비밀번호 (대소문자/숫자/특수문자 (10~16자리))"/>

          <p>마케팅 수신 동의</p>
          <input type="radio" name="agree" id="marketingAgree">
          <label for="marketingAgree" class="ml05">동의</label>

          <input type="radio" name="agree" id="marketingDisAgree" class="ml20">
          <label for="marketingDisAgree" class="ml05">비동의</label>

          <div class="btn_wrap mt30">
            <ul>
              <li class="mr5">
                <router-link to="/login">
                  <button class="button_default_border">취소</button>
                </router-link>
              </li>
              <li>
                <button class="button_default" @click="onSubmit" :disabled="invalid">회원가입</button>
              </li>
            </ul>
          </div>
        </ValidationObserver>

      </section>
    </div>
  </div>
</template>

<script>
import {ValidationObserver} from 'vee-validate'
import InputWithValidation from '@/components/common/validations/inputbox'

export default {
  name: "UserInfo",
  components: {
    ValidationObserver,
    InputWithValidation,
  },
  data() {
    return {
      form: {
        name: '',
        email: '',
        userId: '',
        password: '',
        nickname: ''
      },
    }
  },
  methods: {
    onSubmit() {
    }
  }

}
</script>

